import * as GWU from 'gw-utils';
export const features = {};
export function install(name, fn) {
    if (typeof fn !== 'function') {
        fn = make(fn);
    }
    features[name] = fn;
}
export const types = {};
export function installType(name, fn) {
    types[name] = fn;
}
// FEATURE TYPE
export function feature(id) {
    if (Array.isArray(id))
        id = id[0];
    if (id && typeof id !== 'string') {
        id = id.id;
    }
    if (!id || !id.length)
        throw new Error('Feature effect needs ID');
    return featureFeature.bind(undefined, id);
}
export function featureFeature(id, site, x, y) {
    const feat = features[id];
    if (!feat) {
        throw new Error('Failed to find feature: ' + id);
    }
    return feat(site, x, y);
}
installType('feature', feature);
installType('effect', feature);
installType('id', feature);
export function make(id, config) {
    if (!id)
        return GWU.FALSE;
    if (typeof id === 'string') {
        if (!id.length)
            throw new Error('Cannot create effect from empty string.');
        if (!config) {
            const parts = id.split(':');
            id = parts.shift().toLowerCase();
            config = parts;
        }
        // string with no parameters is interpreted as id of registered feature
        if (config.length === 0) {
            config = id;
            id = 'feature';
        }
        const handler = types[id];
        if (!handler)
            throw new Error('Failed to find effect - ' + id);
        return handler(config || {});
    }
    let steps;
    if (Array.isArray(id)) {
        steps = id
            .map((config) => make(config))
            .filter((a) => a !== null);
    }
    else if (typeof id === 'function') {
        return id;
    }
    else {
        steps = Object.entries(id)
            .map(([key, config]) => make(key, config))
            .filter((a) => a !== null);
    }
    if (steps.length === 1) {
        return steps[0];
    }
    return (site, x, y) => {
        return steps.every((step) => step(site, x, y));
    };
}
export function makeArray(cfg) {
    if (!cfg)
        return [];
    if (Array.isArray(cfg)) {
        return cfg
            .map((c) => make(c))
            .filter((fn) => fn !== null);
    }
    if (typeof cfg === 'string') {
        if (!cfg.length)
            throw new Error('Cannot create effect from empty string.');
        const parts = cfg.split(':');
        cfg = parts.shift().toLowerCase();
        const handler = types[cfg];
        if (!handler)
            return [];
        return [handler(parts)];
    }
    else if (typeof cfg === 'function') {
        return [cfg];
    }
    const steps = Object.entries(cfg).map(([key, config]) => make(key, config));
    return steps.filter((s) => s !== null);
}
